import React from "react";
import {BrowserRouter,Route,Routes} from "react-router-dom";
import App from "./App";
import Generate from "./pages/Generate";
import Playlist from "./pages/Playlist";
import ArtistHome from "./pages/ArtistHome";

function Router(){
    return(
        <BrowserRouter>
            <Routes>
                <Route path="/music/" element={<Generate/>}></Route>
                <Route path="/music/Playlist" element={<Playlist/>}></Route>
                <Route path="/music/Playlist/:id" element={<ArtistHome />} />
            </Routes>
        </BrowserRouter>
    );
}

export default Router;