import React,{useEffect,useState} from "react";
import MusicListData3 from "../music3.json";
import { useParams } from 'react-router-dom';
import Player from "./Player";


function ArtistHome(){

    const [coverimage,setCoverimage] = useState("");
    const [title,setTitle] = useState("");
    const [artistName,setArtistName] = useState("");
    const [music,setMusic] = useState("");
    const [open,setOpen] = useState(false);


    const urlParams = useParams();
    const id = urlParams.id;
    const musicList3 = MusicListData3;
    const artist = musicList3[Number(id) - 1];
    console.log(artist);

    //スクロール位置元に戻す
    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    function handleOpen(coverimage,title,artistName,music){
        setCoverimage(coverimage);
        setTitle(title);
        setArtistName(artistName);
        setMusic(music);
        setOpen(true);
    }

    function handleClose(){
        setCoverimage("");
        setTitle("");
        setArtistName("");
        setOpen(false);
    }

    return(
        <div className="artistHome">
        <Player open={open} handleClose={handleClose} coverimage={coverimage} title={title} artist={artistName} music={music}/>
            <header>
                <div className="artistHomeHeaderImage">
                    <img src={`${artist.coverimage}`} alt="" />
                </div>
                <div className="artistHeader">
                    <p>artist</p>
                    <h1>{artist.artist} </h1>
                    <button>LIKE</button>
                    <button>FOLLOW</button>
                </div>
            </header>
                <div className="artistHomeSubheader">
                    <h1>Popular</h1>
                </div>
                <div className="artistHomeTune" onClick={()=>handleOpen(artist.coverimage,artist.music1title,artist.artist,artist.music1)}>
                    <div className="">
                        <img src={`${artist.coverimage}`} alt="" />
                    </div>
                    <div className="musicTitle">
                        <h3>{artist.music1title}</h3>
                        <p>{artist.artist}</p>
                    </div>
                </div>
                <div className="artistHomeTune">
                    <div className="">
                        <img src={`${artist.coverimage}`} alt="" />
                    </div>
                    <div className="musicTitle">
                        <h3>{artist.music2title}</h3>
                        <p>{artist.artist}</p>
                    </div>
                </div>
        </div>
    );
}

export default ArtistHome;